.h-container {
  width: 100%;
  min-width: 480px;
  height: auto;
  background: #84a178;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.ice-icon {
  width: 40px;
  height: 37px;
  background: red;
}

.menu-toggle svg {
  width: 32px;
  height: 32px;
  background: red;
}

.head-navigation {
  display: none;
}

.nav-list {
  flex-direction: column;
  gap: 28px;
  list-style: none;
  display: flex;
}

.nav-list_element {
  letter-spacing: 0;
  text-align: center;
  color: #fcfcfc;
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
}

.purchase_button {
  width: 152px;
  height: 42px;
  text-align: center;
  background: #f8d24e;
  border-radius: 10px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
}

@media screen and (min-width: 480px) and (max-width: 1199px) {
  .mobile {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .desktop {
    display: none;
  }
}

.menu-container {
  width: 100%;
  height: auto;
  z-index: 999;
  background-color: #84a178;
  padding: 0 40px;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
  position: fixed;
  top: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}















